.topContainer{
  width:100%;
  height:100%;
  position:fixed;
  display:flex;

}

.inputPane{
  left:0;
  overflow-y:scroll;
  box-sizing: border-box;
  width: calc(30% - 30px);
  height: calc(100% - 40px);
  margin:20px 10px 20px 20px;
  border-radius:5px;
  background-color: #e9ebe4;
  border-color:#cf361f;
  position:fixed;
}

.outputPane{
  right:0;
  width: calc(70% - 30px);
  height: calc(100% - 40px);
  margin:20px 20px 20px 10px;
  border-radius:5px;
  background-color:#e9ebe4;
  border-color:#cf361f;
  position:fixed;
}

.invtype{
  display:flex;
}

.outputChart{
  display:flex;
  flex-flow: column;
  height:100%
}

.outputHeader{
  width:100%;
  flex: 0 1 auto;
}

.tester{
  width:100%;
  flex:1 0 auto;
  border:5px solid red;
}

.inputArea{
  margin-left: 15px;
  margin-right: 15px;
}

datalist {
  display: flex;
  justify-content: space-between;
  color: red;
  width: 50%;
}

input {
  width: 50%;
}
